"use client";

import { useState, useCallback, useRef, useEffect } from "react";

export interface PanZoomState {
  zoom: number;
  translateX: number;
  translateY: number;
}

const MIN_ZOOM = 0.5;
const MAX_ZOOM = 5;
const ZOOM_STEP = 0.1;
const DEFAULT_ZOOM = 1;

export function usePanZoom(initialState?: Partial<PanZoomState>) {
  const [state, setState] = useState<PanZoomState>({
    zoom: initialState?.zoom ?? DEFAULT_ZOOM,
    translateX: initialState?.translateX ?? 0,
    translateY: initialState?.translateY ?? 0,
  });

  const isPanning = useRef(false);
  const lastPanPoint = useRef({ x: 0, y: 0 });

  const zoomBy = useCallback((delta: number, centerX?: number, centerY?: number) => {
    setState((prev) => {
      const newZoom = Math.max(MIN_ZOOM, Math.min(MAX_ZOOM, prev.zoom + delta));
      const zoomRatio = newZoom / prev.zoom;

      // If center point is provided, zoom towards that point
      if (centerX !== undefined && centerY !== undefined) {
        const newTranslateX = centerX - (centerX - prev.translateX) * zoomRatio;
        const newTranslateY = centerY - (centerY - prev.translateY) * zoomRatio;
        return {
          zoom: newZoom,
          translateX: newTranslateX,
          translateY: newTranslateY,
        };
      }

      return {
        ...prev,
        zoom: newZoom,
      };
    });
  }, []);

  const panBy = useCallback((deltaX: number, deltaY: number) => {
    setState((prev) => ({
      translateX: prev.translateX + deltaX,
      translateY: prev.translateY + deltaY,
      zoom: prev.zoom,
    }));
  }, []);

  const reset = useCallback(() => {
    setState({
      zoom: DEFAULT_ZOOM,
      translateX: 0,
      translateY: 0,
    });
  }, []);

  const setZoom = useCallback((zoom: number) => {
    setState((prev) => ({
      ...prev,
      zoom: Math.max(MIN_ZOOM, Math.min(MAX_ZOOM, zoom)),
    }));
  }, []);

  const zoomIn = useCallback(() => {
    zoomBy(ZOOM_STEP);
  }, [zoomBy]);

  const zoomOut = useCallback(() => {
    zoomBy(-ZOOM_STEP);
  }, [zoomBy]);

  // Mouse pan handlers
  const handleMouseDown = useCallback((e: React.MouseEvent) => {
    if (e.button === 0) { // Left mouse button
      isPanning.current = true;
      lastPanPoint.current = { x: e.clientX, y: e.clientY };
      e.preventDefault();
    }
  }, []);

  const handleMouseMove = useCallback((e: React.MouseEvent) => {
    if (isPanning.current) {
      const deltaX = e.clientX - lastPanPoint.current.x;
      const deltaY = e.clientY - lastPanPoint.current.y;
      panBy(deltaX, deltaY);
      lastPanPoint.current = { x: e.clientX, y: e.clientY };
      e.preventDefault();
    }
  }, [panBy]);

  const handleMouseUp = useCallback(() => {
    isPanning.current = false;
  }, []);

  const handleWheel = useCallback((e: React.WheelEvent) => {
    e.preventDefault();
    const delta = e.deltaY > 0 ? -ZOOM_STEP : ZOOM_STEP;
    const rect = e.currentTarget.getBoundingClientRect();
    const centerX = e.clientX - rect.left;
    const centerY = e.clientY - rect.top;
    zoomBy(delta, centerX, centerY);
  }, [zoomBy]);

  // Touch handlers
  const touchStartRef = useRef<{ x: number; y: number; distance: number } | null>(null);

  const handleTouchStart = useCallback((e: React.TouchEvent) => {
    if (e.touches.length === 1) {
      touchStartRef.current = {
        x: e.touches[0].clientX,
        y: e.touches[0].clientY,
        distance: 0,
      };
    } else if (e.touches.length === 2) {
      const touch1 = e.touches[0];
      const touch2 = e.touches[1];
      const distance = Math.hypot(
        touch2.clientX - touch1.clientX,
        touch2.clientY - touch1.clientY
      );
      touchStartRef.current = {
        x: (touch1.clientX + touch2.clientX) / 2,
        y: (touch1.clientY + touch2.clientY) / 2,
        distance,
      };
    }
  }, []);

  const handleTouchMove = useCallback((e: React.TouchEvent) => {
    if (!touchStartRef.current) return;

    if (e.touches.length === 1 && touchStartRef.current.distance === 0) {
      // Single finger pan
      const deltaX = e.touches[0].clientX - touchStartRef.current.x;
      const deltaY = e.touches[0].clientY - touchStartRef.current.y;
      panBy(deltaX, deltaY);
      touchStartRef.current.x = e.touches[0].clientX;
      touchStartRef.current.y = e.touches[0].clientY;
    } else if (e.touches.length === 2 && touchStartRef.current.distance > 0) {
      // Two finger pinch zoom
      const touch1 = e.touches[0];
      const touch2 = e.touches[1];
      const distance = Math.hypot(
        touch2.clientX - touch1.clientX,
        touch2.clientY - touch1.clientY
      );
      const scale = distance / touchStartRef.current.distance;
      const rect = e.currentTarget.getBoundingClientRect();
      const centerX = (touchStartRef.current.x - rect.left);
      const centerY = (touchStartRef.current.y - rect.top);
      zoomBy((scale - 1) * ZOOM_STEP * 2, centerX, centerY);
      touchStartRef.current.distance = distance;
    }
  }, [panBy, zoomBy]);

  const handleTouchEnd = useCallback(() => {
    touchStartRef.current = null;
  }, []);

  return {
    state,
    zoomBy,
    panBy,
    reset,
    setZoom,
    zoomIn,
    zoomOut,
    handlers: {
      onMouseDown: handleMouseDown,
      onMouseMove: handleMouseMove,
      onMouseUp: handleMouseUp,
      onMouseLeave: handleMouseUp,
      onWheel: handleWheel,
      onTouchStart: handleTouchStart,
      onTouchMove: handleTouchMove,
      onTouchEnd: handleTouchEnd,
    },
  };
}

