// Helper function to convert BJJConcept to ConceptPoint
// This will be used when we integrate with existing data
export function convertBJJConceptToConceptPoint(
  concept: {
    id: string;
    concept: string;
    description?: string;
    category: string;
    axis_self_opponent: number;
    axis_mental_physical: number;
    color?: string;
  }
): ConceptPoint {
  return {
    id: concept.id,
    label: concept.concept,
    x: concept.axis_self_opponent, // Already normalized -1 to 1
    y: concept.axis_mental_physical, // Already normalized -1 to 1
    category: concept.category,
    description: concept.description,
    color: concept.color,
  };
}

